
          @import "@/assets/css/_mixins.scss";
          @import "@/assets/css/_variables.scss";
          @import "@/assets/css/_mediaQueries.scss";
          @import "@/assets/css/_svg.scss";
          @import "@/assets/css/_common.scss";
          @import "@/assets/fontello/css/fontello.css";
          @import "@/assets/css/reset.css";
        

#header {
  padding-top: 2rem;
  padding-bottom: 6rem;

  .avatar {
    display: inline-block;
    padding: 0.5rem;
    border-radius: 100%;
    background-color: $bgColor;
    border: 1px solid $outlineColor;
    margin-top: 2rem;
    margin-bottom: 2rem;

    img {
      border-radius: 100%;
      display: block;
      width: 170px;
      height: 170px;
    }
  }

  h1 {
    font-size: 1.25em;
    font-weight: 200;
    line-height: 1.5rem;
    color: $titleColor;
    margin: 0 0 1em 0;

    strong {
      color: white;
      font-weight: 200;
    }

    a {
      color: $titleColor;
      border-bottom: 1px dotted $outlineColor;
    }
    a:hover {
      color: white;
    }
  }

  ul.icons li {
    display: inline-block;
    width:3rem;
    height:3rem;
    border-radius: 100%;
    color: $titlebgColor;
    background-color: $titlebgColor;
    border: 1px solid $outlineColor;
    margin-right: 1em;
    display: inline-flex;
    justify-content: center;
    align-items: center;

    :hover {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: inherit;
      height: inherit;
      border-radius: 100%;
      border: 1px solid rgba(255, 255, 255, 0.25);
      background-color: $titlebgColor;
    }

    a.icon {
      color: $titleColor;
      font-weight: 200;
    }

    .label {
      display: none;
    }
  }
}

@media screen and (max-width: 1280px) {
  #header {
    padding: 4em 0 2em 0;
  }
}
